@import url(https://fonts.googleapis.com/css2?family=Courgette&family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Courgette&family=Fredoka+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.dashboard{
    margin-top: 0px;
    padding-top: 0px;
}
.list1{
    background-color: #f44336;
    font-weight: bold;
    font-family: 'Fredoka One', cursive;
}
.title{
    font-size: 150px;
    text-align: center;
    font-family: "Fredoka One", cursive;
    padding-top: 200px;
    text-shadow: -2px 0  #8750b4, 0 2px  #8750b4, 2px 0  #8750b4, 0 -2px  #8750b4;
    color:  whitesmoke;
}
.title:hover{
    letter-spacing: 20px;
    transition-duration: 2s;
}
body{
  background-image: linear-gradient(
    -145deg,
    #801ecf,
    #401374,
    #8750b4,
    #581399
  );
}
.home{
  padding-top: 110px;
  padding-bottom: 50px;
  text-align: center;
}
.form {
  background-color: transparent;
  box-shadow: 0.5rem 0.3rem 1.5rem #47498c, -0.5rem -0.5rem 1.5rem, #5d5fb6;
  border-radius: 2rem;
}
.house:hover{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
.header{
    font-family: 'Fredoka One', cursive;
    letter-spacing: 2px;
    text-shadow: 2px 2px #000000;
    text-transform: uppercase;
      }
.header:hover{
    letter-spacing: 15px;
    transition-duration: 1s;
}
.font {
  font-family: "Courgette", cursive;
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 25px;
}


